.personCart__container-info {
  display: flex;
  width: 1100px;
  height: 500px;
  margin: 80px 0 0 20px;
  background-color: rgb(60, 62, 68);
  border-radius: 7px;
  position: relative;
}

.personCart__container-image {
  img {
    border-radius: 7px;
    height: 500px;
  }
}

.personCart__container-information {
  width: 600px;
  padding: 10px 0 0 10px;
  color: white;
  line-height: 2.7em;
  font-size: 30px;
  cursor: pointer;

  h1:hover {
    color: orange;
  }

  p:hover {
    color: orange;
  }
  span {
    font-size: 30px;
    .status_alive {
      display: inline-block;
      width: 20px;
      height: 20px;
      background-color: greenyellow;
      border-radius: 50%;
      margin-right: 10px;
    }
    .status_dead {
      display: inline-block;
      width: 20px;
      height: 20px;
      background-color: red;
      border-radius: 50%;
      margin-right: 10px;
    }
    .status_unknown {
      display: inline-block;
      width: 20px;
      height: 20px;
      background-color: grey;
      border-radius: 50%;
      margin-right: 10px;
    }
  }
}

button {
  align-items: center;
  width: 150px;
  height: 50px;
  margin: 0 auto;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 6px;
  box-shadow: 3px 5px 0px black;
  transition-duration: 0.5s;
  position: absolute;
  bottom: 8px;
  right: 5px;

  &:hover {
    color: white;
    background-color: orange;
    box-shadow: 1px 3px 0px black;
    margin-top: 2px;
  }
}
