.peopleCard__container {
  width: 300px;
  height: 470px;
  margin-bottom: 50px;
  margin-right: 30px;
  background-color: rgb(60, 62, 68);
  border-radius: 6px;

  img {
    width: 100%;
    border-radius: 6px;
  }

  &_info {
    margin-top: -4px;
    padding: 5px;
    color: white;
    font-size: 20px;
    line-height: 1.5em;

    a {
      text-decoration: none;
    }

    &_name {
      font-size: 30px;
      color: white;
      font-weight: bold;
      cursor: pointer;
      &:hover {
        color: orange;
      }
    }

    span {
      .status_alive {
        display: inline-block;
        width: 10px;
        height: 10px;
        background-color: greenyellow;
        border-radius: 50%;
        margin-right: 10px;
      }
      .status_dead {
        display: inline-block;
        width: 10px;
        height: 10px;
        background-color: red;
        border-radius: 50%;
        margin-right: 10px;
      }
      .status_unknown {
        display: inline-block;
        width: 10px;
        height: 10px;
        background-color: grey;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
  }
}
