@import "Home.scss";
@import "People.scss";
@import "Button.scss";
@import "PersonCart.scss";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: rgb(32, 35, 41);
}

.header {
  background-color: grey;
  width: 100%;
  height: 50px;

  &__container {
    display: flex;
    padding: 5;
    align-items: center;

    a {
      text-decoration: none;
    }
    div {
      margin-left: 10px;
      font-size: 40px;
      color: white;
      font-weight: bold;
      cursor: pointer;
      padding-right: 15px;
      border-right: 5px solid black;
    }

    input {
      margin-left: 20px;
      width: 200px;
      height: 30px;
      font-size: 20px;
      padding-left: 5px;
    }
  }
}
